.nav {
  display: flex;
  position: fixed;
  top: 0;
  width: 100%;
  padding: 20px;

  justify-content: space-between;
  z-index: 1;

  transition-timing-function: ease-in;
  transition: all 0.5s;
}

.nav-black {
  height: 30px;
  background-color: #111;
}

.nav_logo {
  position: fixed;
  left: 20px;
  object-fit: contain;
  width: 80px;
}

.nav_avatar {
  position: fixed;
  right: 20px;
  object-fit: contain;
  width: 30px;
}
